

.App {
  display: flex;
  flex-direction: column;
  width: 100%; 
  height: 100%; 


}

.App-Bar {
  border: 1px solid black;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 3vh;
  display: flex;
  justify-content: space-between;

}


.App-Inputs, .App-Name {
  border: 1px solid black;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 16vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 3vh;



}

.App-Keyboard {
  border: 1px solid black;
  flex-basis: 22vh;


}

#keyboard-cont {

  display: flex;
  flex-direction: column;
  align-items: center;
}

#keyboard-cont div {
  display: flex;
}

.second-row {
  margin: 1vh 0;
}

.App-Keyboard button {
  font-size: 4vmin;
  padding: 1.7vh;
  cursor: pointer;
  text-transform: uppercase;
  background: none;
	color: inherit;
  border: none;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.App-Images {
  border: 1px solid black;
  flex-grow: 0;

  flex-shrink: 0;
  flex-basis: 57vh;
  display: flex;
  flex-direction: column;


}

.App-box:before{
  content: '';
  display: block;
  padding-top: 100%;

}
.App-Namebox:before {
  content: '';
  display: block;
  padding-top: 100%;

}
.App-photo1 {

  height: 16vh;
  width: 47vw;
  padding: 1vh;



}

.App-photo1 img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 3px;
  display: block;
  margin-left: auto;
  margin-right: auto;

}
.App-photo1 p {
  text-align: center;
  color: grey;
  font-size: 10px;


}

.App-photo2 {

  height: 16vh;
  width: 47vw;
  padding: 1vh;


}

.App-photo2 img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 3px;
  display: block;
  margin-left: auto;
  margin-right: auto;

}
.App-photo2 p {
  text-align: center;
  color: grey;
  font-size: 10px;


}

.App-photo3 {
  padding: 2vh;




}

.App-photo3 img {
  max-width: 98%;
  border-radius: 6px;
  max-height: 38vh;
  display: block;
  margin-left: auto;
  margin-right: auto;

}
.App-photo3 p {
  text-align: center;
  color: grey;
  font-size: 10px;
}

.App-row1 {
  display: flex;
  flex-direction: row;
}

.barbutton {

    font-size: 2vmin;
    cursor: pointer;
    padding: 0.5vh;
    background: none;
    color: inherit;
    border: none;
    font: inherit;
    cursor: pointer;
    outline: inherit;

}

.App-Leaderboard{
  border: 1px solid black;
  flex-grow: 0;

  flex-shrink: 0;
  flex-basis: 57vh;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  -ms-overflow-style: none;  
  scrollbar-width: none;  
  
}

.App-Leaderboard::-webkit-scrollbar {
  display: none;
}


.leaderboardmem {
  border: 1px solid black;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-around;

}


[status = "false"] {
  border: 1px solid black;
  color: black;
}

[status = "true"] {
  border: 1px solid white;
  color: white;
}